import React from "react";
import {useState} from "react";
import Info from "./Modals/Info";


export default function () {

    const [show, setShow] = useState(false);

    return (<div>
        <hr className={"w-full"} style={{color: "white", height: "4px"}}/>

        <div className={"text-2xl text-white text-center"}>  ,    !</div>
        <div className="grid btns gap-2 place-content-center m-2" style={{marginTop: "15px", marginBottom: "5px", display: "flex", justifyContent: "center"}}>
            <button type="button" className="btn btn-danger btn-lg"  style={{width: "200px"}} onClick={() => setShow(true)}> </button>
        </div>

        <div className={"text-gray-400  text-sm text-center mb-5"}>    (DHCPCD)   (ItsOlegDm)</div>
        <div className={"text-gray-400 text-sm text-center mb-5"}>   '  Lisek Vtuber.<br />     -    lisekworld@gmail.com   </div>


        <Info show={show} onHide={() => setShow(false)} variant="dark" />
    </div>)

}